import React, {Component} from 'react';
import UseServer from '../../service/UseServer';
import './userView.css';
import SpinnerView from './spinner/spinner'

export default class UserView extends Component {
    server = new UseServer()
    state = {
        bus: [],
        people: [],
        loading: true
    }
    componentDidMount() {
        this.updateData();
    }

    updateData = () => {
        this.server.getBusesFromServer()
            .then(bus => this.setState({bus}))
        this.server.getPeopleFromServer()
            .then(people => {
                this.setState({people: people, loading: false});
            })
    }

    render(){
        let inCity = [];
        let fromCity = [];
        this.state.people.forEach((pers) => {
            if(+pers.people_place){
                if(pers.people_where === 'На берег' || pers.people_where === 'Без возврата'){
                    inCity.push(pers);
                } else {
                    fromCity.push(pers)
                }
            }
        });
        const busMap = this.state.bus.map((item) => {
            let peopleReady = [];
            for(let i = 0; i <= item.count_place - 1; i++){
                let persInCity = '';
                let persFromCity = '';
                inCity.forEach((inCit) => {
                    if(inCit.people_bus === item.bus_name){
                        if(+inCit.people_place === (i+1)){
                            persInCity = inCit.people_name;
                        }
                    }
                });
                fromCity.forEach((fromCit) => {
                    if(fromCit.people_bus === item.bus_name){
                        if(+fromCit.people_place === (i+1)){
                            persFromCity = fromCit.people_name;
                        }
                    }
                });
                peopleReady.push((
                    <tr key={i}>
                        <td>{i+1}</td>
                        <td>{persInCity}</td>
                        <td>{persFromCity}</td>
                    </tr>
                ));
            }
            return(
                    <table className="userTable" key={item.bus_name}>
                        <tbody>
                        <tr>
                            <th colSpan="3">
                                {item.bus_name} <br/>
                                {new Date(item.date_bus).toLocaleString(
                                    'ru-RU',
                                    {
                                        day: 'numeric',
                                        month: 'numeric',
                                        year: 'numeric'
                                    })}
                            </th>
                        </tr>
                        {peopleReady}
                        </tbody>
                    </table>
            )
        });
        const spinnerMap = this.state.loading ? <SpinnerView/> : busMap
        return(
            <div className='userView'>
                {spinnerMap}
            </div>
        )
    }
}

